import React, { useState, useEffect } from "react"
import { Link } from "react-router-dom"

import { useDispatch, useSelector } from "react-redux"
import {
  Row,
  Col,
  Image,
  ListGroup,
  Card,
  Button,
  ListGroupItem,
  Form,
} from "react-bootstrap"
import Rating from "../components/Rating"
import Message from "../components/Message"
import Meta from "../components/Meta"
import Loader from "../components/Loader"
import {
  listProductDetails,
  createProductReview,
} from "../actions/productAction"
import { PRODUCT_CREATE_REVIEW_RESET } from "../constants/productConstants"
import ImageZoom from "../components/ImageZoom"
const ProductScreen = ({ history, match }) => {
  const [qty, setQty] = useState(1)
  const [rating, setRating] = useState(0)
  const [comment, setComment] = useState("")
  //const [data, setData] = useState("")
  const dispatch = useDispatch()

  const productDatails = useSelector((state) => state.productDetails)
  const { loading, error, product } = productDatails

  const userLogin = useSelector((state) => state.userLogin)
  const { userInfo } = userLogin

  const productReviewCreate = useSelector((state) => state.productReviewCreate)
  const {
    success: successProductReview,
    error: errorProductReview,
  } = productReviewCreate

  useEffect(() => {
    if (successProductReview) {
      alert("review submitted")
      setRating(0)
      setComment("")
      dispatch({ type: PRODUCT_CREATE_REVIEW_RESET })
    } else {
    }

    dispatch(listProductDetails(match.params.id))
  }, [dispatch, match, successProductReview])

  const addToCartHandler = () => {
    history.push(`/cart/${match.params.id}?qty=${qty}`)
  }

  const submitHandler = (e) => {
    e.preventDefault()

    dispatch(createProductReview(match.params.id, { rating, comment }))
  }

  return (
    <>
      <Link className='btn btn-light my-4' to='/'>
        Regresar
      </Link>

      {loading ? (
        <Loader></Loader>
      ) : error ? (
        <Message variant='danger' booltemporary='false'>
          {error}
        </Message>
      ) : (
        <>
          <Meta title={product.name}></Meta>
          <Row>
            <Col md={6}>
              <ImageZoom images={product.image}></ImageZoom>
            </Col>

            <Col md={3}>
              <ListGroup variant='flush'>
                <ListGroup.Item>
                  <h2>{product.name}</h2>
                </ListGroup.Item>
                <ListGroup.Item>
                  <Rating
                    value={product.rating}
                    text={`${product.numReviews} reseas`}
                  ></Rating>
                </ListGroup.Item>

                <ListGroup.Item>Precio:${product.price}</ListGroup.Item>
                <ListGroup.Item>
                  Descripcin:${product.description}
                </ListGroup.Item>
              </ListGroup>
            </Col>
            <Col md={3}>
              <Card>
                <ListGroup>
                  <ListGroup.Item>
                    <Row>
                      <Col> Precio:</Col>
                      <Col>
                        <strong> ${product.price}</strong>
                      </Col>
                    </Row>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col> Stock:</Col>
                      <Col>
                        {product.countInStock > 0
                          ? "In Stock"
                          : "out of stock1"}
                      </Col>
                    </Row>
                  </ListGroup.Item>
                  {product.countInStock > 0 && (
                    <ListGroupItem>
                      <Row>
                        <Col>Qty</Col>
                        <Col>
                          <Form.Control
                            as='select'
                            value={qty}
                            onChange={(e) => setQty(e.target.value)}
                          >
                            {[...Array(product.countInStock).keys()].map(
                              (x) => (
                                <option key={x + 1} value={x + 1}>
                                  {x + 1}
                                </option>
                              )
                            )}
                          </Form.Control>
                        </Col>
                      </Row>
                    </ListGroupItem>
                  )}
                  <ListGroup.Item>
                    <Button
                      onClick={addToCartHandler}
                      className='btn-block'
                      type='button'
                      disabled={product.countInStock === 0}
                    >
                      Add to cart
                    </Button>
                  </ListGroup.Item>
                </ListGroup>
              </Card>
            </Col>
          </Row>
          <Row>
            <Col md={6}>
              <h2>Reseas</h2>
              {product.reviews.length === 0 && <Message>No reviews</Message>}
              <ListGroup variant='flush'>
                {product.reviews.map((review) => (
                  <ListGroup.Item key={review._id}>
                    <strong>{review.name}</strong>
                    <Rating value={review.rating} text=''></Rating>
                    <p>{review.createdAt.substring(0, 10)}</p>
                    <p>{review.comment}</p>
                  </ListGroup.Item>
                ))}
                <ListGroup.Item>
                  <h2>Escribe una resea</h2>
                  {errorProductReview && (
                    <Message variant='danger' booltemporary='true'>
                      {errorProductReview}
                    </Message>
                  )}
                  {userInfo ? (
                    <Form onSubmit={submitHandler}>
                      <Form.Group controlId='rating'>
                        <Form.Label>Clasificacin</Form.Label>
                        <Form.Control
                          as='select'
                          onChange={(e) => setRating(e.target.value)}
                        >
                          <option value=''>Select..</option>
                          <option value='1'>1 - Pobre</option>
                          <option value='2'>2 - Justo</option>
                          <option value='3'>3 - Bueno</option>
                          <option value='4'>4 - Muy bueno</option>
                          <option value='5'>5 - Excelente</option>
                        </Form.Control>
                      </Form.Group>
                      <Form.Group controlId='comment'>
                        <Form.Label>Comentarios</Form.Label>
                        <Form.Control
                          as='textarea'
                          row='3'
                          value={comment}
                          onChange={(e) => setComment(e.target.value)}
                        ></Form.Control>
                      </Form.Group>
                      <Button type='submit' variant='primary'>
                        Enviar
                      </Button>
                    </Form>
                  ) : (
                    <Message booltemporary='false'>
                      please <Link to='/login'>sing in</Link> to writte a review
                    </Message>
                  )}
                </ListGroup.Item>
              </ListGroup>
            </Col>
          </Row>
        </>
      )}
    </>
  )
}

export default ProductScreen
